function showAlert(message, okAction, cancelAction) {
    const alertElement = document.getElementById('my-alert');
    const messageElement = document.getElementById('alert-message');
    const okButton = document.getElementById('alert-ok-button');
    const cancelButton = document.getElementById('alert-cancel-button');
  
    // Set the message text
    messageElement.textContent = message;
  
    // Show the alert element
    alertElement.classList.remove('hidden');
  
    // Register event listeners for OK and cancel buttons
    okButton.addEventListener('click', (event) => {
      if (okAction) {
        okAction();
      }
      alertElement.classList.add('hidden');
    });
  
    cancelButton.addEventListener('click', (event) => {
      if (cancelAction) {
        cancelAction();
      }
      alertElement.classList.add('hidden');
    });
  }
  export {showAlert}